	export default {
		data() {
			return {
				deviceid: '',
                msgArr: [],
                hasMsg: true,
                hasNet: false,
                style: {
                	pageHeight: 0,
                	contentViewHeight: 0
                }
			}
		},
        onShow() {
            let deviceid = this.$store.state.deviceId;
            const res = uni.getSystemInfoSync();
            this.style.pageHeight = res.windowHeight;
            this.style.contentViewHeight = res.windowHeight; //像素
            uni.request({
                url: this.websiteUrl + '/api/sysmsg/list',
                method: 'GET',
                data: {
                    deviceId: deviceid
                },
                success: res => {
                    this.msgArr = res.data.list;
                    if (this.msgArr.length > 0) {
                        this.hasMsg = false;
                        this.hasNet = false;
                    } else{
                        this.hasMsg = true;
                        this.hasNet = false;
                    }
                },
                fail: () => {
                    this.hasMsg = false;
                    this.hasNet = true;
                },
                complete: (res) => {
                    if (res.data.count > 0) {
                        uni.request({
                            url: this.websiteUrl + '/api/sysmsg/upd',
                            method: 'POST',
                            data: {
                                deviceId: deviceid
                            },
                            success: res => {
                                console.log(res.data.message);
                            },
                            fail: () => {},
                            complete: () => {}
                        });
                    }
                }
            });
        }
	}
